<template>
  <div class="image-layout">
    <el-row>
      <el-col :span="24">
        <el-upload
            class="upload-demo"
            :show-file-list="false"
            multiple
            :on-change="handleChange"
        >
          <el-button slot="trigger" size="small" type="primary">选取图片</el-button>
        </el-upload>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-radio-group v-model="layout" @change="handleLayoutChange">
          <el-radio-button label="3*3">3*3</el-radio-button>
          <el-radio-button label="3*4">3*4</el-radio-button>
          <el-radio-button label="4*3">4*3</el-radio-button>
          <el-radio-button label="2*3">2*3</el-radio-button>
          <el-radio-button label="3*2">3*2</el-radio-button>
          <el-radio-button label="2*2">2*2</el-radio-button>
        </el-radio-group>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div class="image-container" ref="imageContainer">
          <draggable v-model="images" :options="{group:'image'}" @end="handleDragEnd">
<!--            <div-->
<!--                v-for="(image, index) in images"-->
<!--                :key="index"-->
<!--                class="image-item"-->
<!--                :style="{width: image.width + 'px', height: image.height + 'px', transform: 'rotate(' + image.rotate + 'deg)'}"-->
<!--            >-->
            <div
                v-for="(image, index) in images"
                :key="index"
                class="image-item"
                :style="{width: '200px', height: '200px', transform: 'rotate(' + image.rotate + 'deg)'}"
            >
              <img :src="image.url" :style="{transform: 'scale(' + image.scale + ')'}" />
              <div class="image-item-toolbar">
                <el-button size="mini" type="text" @click="handleRotateLeft(index)"><i class="el-icon-arrow-left"></i></el-button>
                <el-button size="mini" type="text" @click="handleRotateRight(index)"><i class="el-icon-arrow-right"></i></el-button>
                <el-button size="mini" type="text" @click="handleFlipX(index)"><i class="el-icon-arrow-up"></i></el-button>
                <el-button size="mini" type="text" @click="handleFlipY(index)"><i class="el-icon-arrow-down"></i></el-button>
                <el-button size="mini" type="text" @click="handleDelete(index)"><i class="el-icon-delete"></i></el-button>
              </div>
            </div>
          </draggable>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import draggable from 'vuedraggable'

export default {
  components: {
    draggable
  },
  data() {
    return {
      layout: '3*3',
      images: []
    }
  },
  methods: {
    handleChange(file) {
      const reader = new FileReader()
      reader.readAsDataURL(file.raw)
      reader.onload = () => {
        const image = new Image()
        image.src = reader.result
        image.onload = () => {
          const width = image.width
          const height = image.height
          const url = reader.result
          const rotate = 0
          const scale = 1
          this.images.push({
            width,
            height,
            url,
            rotate,
            scale
          })
        }
      }
    },
    handleLayoutChange(layout) {
      const [row, col] = layout.split('*').map(Number)
      const imageCount = this.images.length
      if (imageCount > row * col) {
        this.images.splice(row * col, imageCount - row * col)
      } else if (imageCount < row * col) {
        const addCount = row * col - imageCount
        for (let i = 0; i < addCount; i++) {
          const width = 0
          const height = 0
          const url = ''
          const rotate = 0
          const scale = 1
          this.images.push({
            width,
            height,
            url,
            rotate,
            scale
          })
        }
      }
    },
    handleDragEnd() {
      this.$nextTick(() => {
        const imageContainer = this.$refs.imageContainer
        const containerWidth = imageContainer.clientWidth
        const containerHeight = imageContainer.clientHeight
        this.images.forEach(image => {
          const { width, height } = image
          const scale = Math.min(containerWidth / width, containerHeight / height, 1)
          image.scale = scale
        })
      })
    },
    handleRotateLeft(index) {
      this.images[index].rotate -= 90
    },
    handleRotateRight(index) {
      this.images[index].rotate += 90
    },
    handleFlipX(index) {
      this.images[index].rotate += 180
    },
    handleFlipY(index) {
      this.images[index].rotate += 180
    },
    handleDelete(index) {
      this.images.splice(index, 1)
    }
  }
}
</script>