<template>
  <div id="app">
    <el-header></el-header>
    <el-aside></el-aside>
    <el-main>
      <el-row :gutter="20" class="img-list">
        <el-col :span="span" v-for="(item, index) of sortList" :key="item.id">
          <img :src="item.url" alt="" draggable @click="handleImgClick(item)">
        </el-col>
      </el-row>
      <el-dialog title="调整图片" :visible.sync="dialogVisible" width="50%">
        <img :src="activeImageUrl" ref="img" style="width: 100%;" draggable>
        <el-slider v-model="sliderValue" :min="0" :max="360" :step="1" show-input @change="handleSliderChange"></el-slider>
        <el-switch v-model="flipX" active-color="#13ce66" inactive-color="#ff4949" @change="handleFlipChange"></el-switch>
      </el-dialog>
      <el-upload
          class="upload-demo"
          ref="upload"
          :on-success="handleUploadSuccess"
          :file-list="fileList"
      >
        <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
        <el-button style="margin-left: 10px;" size="small" type="success" @click="handleUpload()">上传到服务器</el-button>
      </el-upload>
      <draggable v-model="sortList" :options="options" @change="handleSortChange">
        <el-col :span="span" v-for="(item, index) of sortList" :key="item.id">
          <img :src="item.url" alt="" draggable>
        </el-col>
      </draggable>
      <el-button type="primary" style="margin-right: 20px;" @click="handleExport()">导出布局</el-button>
    </el-main>
    <el-footer>
      <el-tabs v-model="layout" tab-position="top" type="border-card" style="margin-top: 20px;">
        <el-tab-pane label="3*3布局" name="3-3">
          <el-row :gutter="20">
            <el-col :span="8" v-for="(item, index) of sortList" :key="item.id">
              <img :src="item.url" alt="" draggable>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="3*4布局" name="3-4">
          <el-row :gutter="20">
            <el-col :span="6" v-for="(item, index) of sortList" :key="item.id">
              <img :src="item.url" alt="" draggable>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="4*3布局" name="4-3">
          <el-row :gutter="20">
            <el-col :span="6" v-for="(item, index) of sortList" :key="item.id">
              <img :src="item.url" alt="" draggable>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="2*3布局" name="2-3">
          <el-row :gutter="20">
            <el-col :span="12" v-for="(item, index) of sortList" :key="item.id">
              <img :src="item.url" alt="" draggable>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="3*2布局" name="3-2">
          <el-row :gutter="20">
            <el-col :span="8" v-for="(item, index) of sortList" :key="item.id">
              <img :src="item.url" alt="" draggable>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="2*2布局" name="2-2">
          <el-row :gutter="20">
            <el-col :span="12" v-for="(item, index) of sortList" :key="item.id">
              <img :src="item.url" alt="" draggable>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-form :model="form" style="margin: 20px 0;">
        <el-form-item label="布局设置">
          <el-radio-group v-model="span" @change="handleSpanChange">
            <el-radio-button :label="8">3*3</el-radio-button>
            <el-radio-button :label="6">3*4/4*3</el-radio-button>
            <el-radio-button :label="12">2*2/2*3/3*2</el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </el-footer>
  </div>
</template>

<script>
import html2canvas from 'html2canvas';
import draggable from 'vuedraggable';

export default {
  components: { draggable }, // 在组件中注册draggable
  data() {
    return {
      uploadUrl: '',
      fileList: [], // 上传的文件列表
      sortList: [], // 排序后的图片列表
      options: { // Draggable组件的配置选项
        handle: "img",
        animation: 300
      },
      span: 8, // 每个格子宽度
      layout: '3-3', // 当前显示的布局
      form: {}, // 表单数据
      dialogVisible: false, // Dialog弹窗是否显示
      activeImageUrl: '', // 当前选中图片的URL
      sliderValue: 0, // Slider的值
      flipX: false, // Switch是否选中
    };
  },
  computed: {
    // 根据布局设置计算每个格子的宽度
    spanWidth() {
      return 24 / this.span;
    }
  },
  methods: {
    // 处理上传成功的回调函数
    handleUploadSuccess(response, file, fileList) {
      console.log(response, file, fileList);
      const { data: { url } } = response;
      this.fileList.push({
        id: Date.now(),
        url,
      });
      this.sortList = [...this.fileList];
    },
    // 处理上传到服务器的函数
    handleUpload() {
      this.$refs.upload.submit();
    },
    // 处理排序改变的函数
    handleSortChange() {
      this.fileList = [...this.sortList];
    },
    // 处理图片点击的函数
    handleImgClick(item) {
      this.activeImageUrl = item.url;
      this.dialogVisible = true;
    },
    // 处理Slider的改变函数
    handleSliderChange(value) {
      this.$refs.img.style.transform = `rotate(${value}deg) scaleX(${this.flipX ? -1 : 1})`;
    },
    // 处理Switch的改变函数
    handleFlipChange(value) {
      this.flipX = value;
      this.$refs.img.style.transform = `rotate(${this.sliderValue}deg) scaleX(${value ? -1 : 1})`;
    },
    // 处理导出布局的函数
    handleExport() {
      html2canvas(document.querySelector('.img-list'), {
        logging: true,
        useCORS: true,
      }).then(canvas => {
        const link = document.createElement('a');
        link.download = 'layout.png';
        link.href = canvas.toDataURL();
        link.click();
      });
    },
    // 处理每个格子宽度改变的函数
    handleSpanChange(value) {
      this.span = value;
    },
  },
};
</script>

<style scoped>
.img-list {
  margin-top: 20px;
  border: 1px solid #ccc;
  padding: 10px;
}
.img-list img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
  cursor: move;
}
.el-form-item {
  margin-bottom: 0;
}
</style>