<template>
  <div>
    <el-row class="operator">
      <input type="file" @change="handleFileUpload" multiple>
<!--      <span>当前选中第{{selectedIndex+1}}张</span>-->
      每行：<el-input-number  size="mini"  v-model="imageCountEveryRow" controls-position="right" :min="1" :max="24" :step="1"></el-input-number>张

      <span v-if="selectedImage || selectAllImage">
        缩放设置：<el-input-number v-if="selectedImage"  size="mini" v-model="selectedImage.showScale" controls-position="right" @change="handleScaleChange" :min="0.1" :max="2" :step="0.1"></el-input-number>
        缩放设置：<el-input-number v-if="selectAllImage"  size="mini" v-model="allImageShowScale" controls-position="right" @change="handleScaleChange" :min="0.1" :max="2" :step="0.1"></el-input-number>
        <!--      高度设置：<el-input-number v-model="selectedImage.showHeight" controls-position="right" @change="handleHeightChange"></el-input-number>-->
        <!--      宽度设置：<el-input-number v-model="selectedImage.showWidth" controls-position="right" @change="handleWidthChange"></el-input-number>-->
        <el-button class="btn" type="primary" size="mini"  @click="rotateImage">旋转</el-button>
        <el-button class="btn" type="danger" size="mini"  @click="removeImage">删除</el-button>
      </span>
      <el-button class="btn" v-if="images.length>0" type="danger" size="mini"  @click="removeAllImage">清空</el-button>
      <el-button class="btn" v-if="images.length>0" type="success" size="mini" @click="handleExport">导出</el-button>

      <el-checkbox class="btn" v-model="selectAllImage">全选</el-checkbox>

    </el-row>


    <el-row class="image-container" v-sortable="{animation: 150}">
<!--      <el-col :span="spanSize" v-for="(image, index) in images" :key="index" style="overflow: scroll;max-height: 500px" >-->
<!--&lt;!&ndash;        {{image.showHeight}}&ndash;&gt;-->
<!--        <img :src="image.src" :height="image.showHeight" :width="image.showWidth" :style="{transform:'rotate('+image.currentRotate+'deg)'}" alt="uploaded image" @click="selectImage(image)">-->
<!--      </el-col>-->
      <div v-for="(image, index) in images" :key="index" :style="{width: spanWidth}" class="image-div" >
<!--        {{image.showHeight}}-->
        <img :src="image.src" :height="image.showHeight" :width="image.showWidth" :style="{transform:'rotate('+image.currentRotate+'deg)'}" alt="uploaded image" @click="selectImage(image)">
      </div>
    </el-row>
  </div>
</template>

<script>
import Sortable from 'sortablejs'
import html2canvas from "html2canvas";
export default {
  computed: {
    spanSize: function(){
      return this.totalColEveryRow / this.imageCountEveryRow
    },
    spanWidth: function(){
      return Math.round(100/this.imageCountEveryRow).toFixed(4)+'%'
    }
  },
  data() {
    return {
      images: [],
      totalColEveryRow: 24,
      imageCountEveryRow: 3,
      selectedIndex: 0,
      allImageShowScale: 1,
      selectedImage: null,
      selectAllImage: false
    }
  },
  methods: {
    handleFileUpload(event) {
      const files = event.target.files;
      for (let i = 0; i < files.length; i++) {
        const reader = new FileReader();
        reader.readAsDataURL(files[i]);
        reader.onloadend = () => {
          const image = new Image();
          image.src = reader.result;
          image.onload = () => {
            image.showHeight = image.height
            image.showWidth = image.width
            image.currentRotate = 0
            image.showScale = 1
            this.images.push(image);
          }

        };
      }
    },
    // selectImage
    selectImage(image) {
      this.selectedImage = image
      this.selectedIndex = this.images.indexOf(image)
    },
    rotateImage() {
      if(this.selectAllImage){
        this.images.forEach(image=>{
          image.currentRotate = (image.currentRotate+90)%360;
        })
      } else {
        this.selectedImage.currentRotate = (this.selectedImage.currentRotate+90)%360;
      }
      this.$forceUpdate()
    },
    removeImage() {
      this.images.splice(this.selectedIndex, 1)
    },
    removeAllImage() {
      this.images = []
    },
    handleHeightChange(currentValue, oldValue) {
      console.log(currentValue, oldValue)
      // this.images[this.selectedIndex].showHeight = currentValue
      this.$forceUpdate()
    },
    handleScaleChange(currentValue, oldValue) {
      console.log(currentValue, oldValue)
      if(this.selectAllImage){
        this.images.forEach(image=>{
          image.showHeight = currentValue * image.height
          image.showWidth = currentValue * image.width
        })
      } else {
        this.selectedImage.showHeight = currentValue * this.selectedImage.height
        this.selectedImage.showWidth = currentValue * this.selectedImage.width
      }
      this.$forceUpdate()
    },
    handleWidthChange(currentValue, oldValue) {
      console.log(currentValue, oldValue)
      this.$forceUpdate()
    },
    handleExport() {
      html2canvas(document.querySelector('.image-container'), {
        logging: true,
        useCORS: true,
      }).then(canvas => {
        const link = document.createElement('a');
        link.download = 'layout.png';
        link.href = canvas.toDataURL();
        link.click();
      });
    }
  },
  directives: {
    sortable: {
      inserted: function (el, binding) {
        new Sortable(el, binding.value || {})
      }
    }
  }
}
</script>

<style>
.image-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.image-container div {
  /*margin: 0px;*/
}

.image-container img {
  /*max-width: 200px;*/
  /*height: 500px;*/
  /*overflow-y: scroll;*/
}
.image-div{
  display: inline-block;
  overflow: hidden;
  max-height: 500px
}

.operator{
  padding: 20px;
}
.btn{
  margin-left: 5px !important;
}
</style>
